.App{
    margin: 0;
    padding: 0;
}

.row {
    margin: 0;
}

.box{
    border: 1px solid #eee;
    border-radius: 12px;
    padding: 12px;
    background: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}


.box2{
    border: 1px solid #eee;
    border-radius: 12px;
    padding: 12px;
    background: #b7d2ff;
    box-shadow: 0 4px 8px #b7d2ff;
    overflow: hidden;
}

::-webkit-scrollbar {
    width: 3px;
}

::-webkit-scrollbar-track {
    background: #b7d2ff;
}

::-webkit-scrollbar-thumb {
    background: #76a8ff;
    border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
    background: #0a58ca;
}







